@import 'vars.scss';
@import 'helpers.scss';
@import 'base.scss';
@import 'calendar.scss';
@import 'todo.scss';

html {
    font-size: 18px;
    font-weight: 400;
    font-family: $font-family;
    color: #333;
}

body {
    background-image: linear-gradient(to bottom, #334, #446);
    background-size: cover;
    background-attachment: fixed;
    margin: 0;
}

[v-cloak] {
    display: none !important;
}

.app {
    margin: 0;
    text-align: center;
}

.toolbar {
    background: #eee;
    height: $toolbar-height;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
    font-size: .9rem;
    padding: 0 0 0 1rem;
    text-align: left;
    box-shadow: 0 1px 3px rgba(#000, 0.2);
    h1 {
        font-size: .9rem;
        line-height: 2rem;
        font-weight: 600;
        margin: 0;
        display: inline;
    }
}

.full-button {
    @include button;
    background: transparent;
    position: absolute;
    right: 0;
    top: 0;
    color: #888;
    padding: 0 .5rem;
    height: $toolbar-height;
    transition: 150ms ease;
    &:hover {
        background: rgba(#000, 0.1);
    }
    &:active {
        background: rgba(#000, 0.2);
        box-shadow: inset 0 2px 10px rgba(#000,0.25);
    }
}

.task-button {
    @include button;
    position: absolute;
    z-index: 9;
    bottom: 2rem;
    right: 2rem;
    height: 3rem;
    width: 3rem;
    border-radius: 4px;
    background: $primary;
}

.window {
    overflow: auto;
    height: calc(100% - #{$toolbar-height});
    margin-top: $toolbar-height;
}

.row {
    display: flex;
    background: #ddd;
    min-height: 100%;
}

td.selected {
    box-shadow: inset 0 0 0 2px $primary;
}